import lunr from 'lunr';
import articles from './2005/2005-articles.json';
// import './2005/2005-auteurs.json';
// import './2005/2005-organismes.json';
// import './2005/2005-publicationshal.json';
console.log(">>>>>>>> app.js")
console.log(articles)

let indexTitle = lunr(function () {
    this.ref('id')
    this.field('title')
    this.field('keywords')
    this.field('abstract', {
        extractor: function (doc = {}) {
            return doc.abstract
        }
    })
    this.field('name', {
        extractor: function (doc = {}) {
            let r = ''
            doc.authors.forEach(e =>
                r = r + e.name + ' '
            )
            return r
        }
    })

    articles.forEach(function (art) {
        this.add(art)
    }, this)
})

export function printTable() {
    let table = document.getElementById("table");
    // clear table
    while (table.childNodes.length > 1) {
        table.removeChild(table.lastChild);
    }

    //get search string
    let search = document.getElementById("searchbox").value;
    console.log(">>>> searching for " + search);

    let result = indexTitle.search(search);
    console.log(">>>> result");
    console.log(result);

    if (result.length == 0) {
        // id col
        let tr = document.createElement('tr');
        let td = document.createElement('td');
        td.innerHTML = "N/A";
        tr.appendChild(td);

        //content col
        td = document.createElement('td');
        td.innerHTML = "no result";
        tr.appendChild(td);
        table.appendChild(tr);
    } else {
        result.forEach(function (r) {
            // id col
            let tr = document.createElement('tr');
            let td = document.createElement('td');
            td.innerHTML = r.ref;
            tr.appendChild(td);

            //content col
            td = document.createElement('td');
            td.innerHTML = JSON.stringify(getDataById(r.ref));
            tr.appendChild(td);
            table.appendChild(tr);
        })
    }

}

function getDataById(id) {

    for (var i = 0; i < articles.length; i++) {
        if (Number(articles[i].id) == Number(id)) {
            return articles[i];
        }
    }

}

